// File: frontend/src/actions/messageActions.js

import { FETCH_MESSAGES, ADD_MESSAGE, FETCH_MEDIA_FOR_MESSAGES } from './types';

const API_USER_URL = 'https://ppacilyoncentre.com/api/users';
const API_MEDIA_URL = 'https://ppacilyoncentre.com/api/media';
const API_UPLOAD_URL = 'https://ppacilyoncentre.com/api/upload';

export const fetchMessages = () => {
  return async dispatch => {
    try {
      const response = await fetch(`${API_USER_URL}/messages/`, {
        method: 'GET',
        headers: {
          'Authorization': `Bearer ${localStorage.getItem('token')}`,
          'Content-Type': 'application/json'
        }
      });
      const data = await response.json();
      dispatch({ type: FETCH_MESSAGES, payload: data });
    } catch (error) {
      console.error('Erreur lors de la rcupration des messages', error);
    }
  };
};

export const fetchMediaForMessages = (messageIds) => async (dispatch) => {
  try {
    const mediaData = {};
    for (const messageId of messageIds) {
      const response = await fetch(`${API_MEDIA_URL}/message/${messageId}`);
      const data = await response.json();
      mediaData[messageId] = data;
    }
    dispatch({ type: 'FETCH_MEDIA_SUCCESS', payload: mediaData });
  } catch (error) {
    console.error(' Erreur lors du chargement des mdias:', error);
    dispatch({ type: 'FETCH_MEDIA_ERROR', error });
  }
};

export const addMessage = (formData) => {
  return async dispatch => {
    try {
      const messageData = {
        tittle: formData.get('tittle'),
        content: formData.get('content'),
      };

      await fetch(`${API_USER_URL}/messages`, {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${localStorage.getItem('token')}`,
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(messageData),
      });

      const mediaFormData = new FormData();
      if (formData.get('image')) {
        mediaFormData.append('image', formData.get('image'));
      }
      if (formData.get('video')) {
        mediaFormData.append('video', formData.get('video'));
      }

      if (formData.get('image') && formData.get('video')) {
        await fetch(`${API_UPLOAD_URL}`, {
          method: 'POST',
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('token')}`,
          },
          body: mediaFormData,
        });
      }

      dispatch(fetchMessages());
    } catch (error) {
      console.error("Erreur lors de l'ajout du message", error);
    }
  };
};
