import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { fetchMessages } from '../../actions/messageActions';
import '../messagelist/MessageList.css';

const MEDIA_BACKEND_URL = 'https://ppacilyoncentre.com/api/getMedia';

const MessageList = () => {
  const dispatch = useDispatch();
  const messages = useSelector((state) => state.messages.messages);

  useEffect(() => {
    dispatch(fetchMessages());
  }, [dispatch]);

  useEffect(() => {
    if (!messages) {
      console.warn(' messages est undefined');
      return;
    }
    if (messages.length === 0) {
      console.info(' Aucun message  afficher');
      return;
    }

    const validMessages = Array.isArray(messages)
      ? messages.filter((msg) => msg && msg.id)
      : [];

    if (validMessages.length === 0) {
      console.info(' Aucun message  afficher: validMessages.length === 0');
      return;
    }

    const fetchMediaForMessages = async () => {
      try {
        const enrichedMessages = await Promise.all(
          validMessages.map(async (message) => {
            const response = await fetch(`${MEDIA_BACKEND_URL}/${message.id}`);
            if (!response.ok) {
              console.warn(`Media non trouv pour le message ${message.id}`);
              return { ...message, media: [] };
            }
            const mediaData = await response.json();
            return { ...message, media: mediaData || [] };
          })
        );

        dispatch({ type: 'UPDATE_MESSAGES', payload: enrichedMessages });
      } catch (error) {
        console.error('Erreur lors du fetch des mdias :', error);
      }
    };

    fetchMediaForMessages();
  }, [messages]);

  return (
    <div className="messagelist">
      <h2> Liste des articles</h2>

      {!Array.isArray(messages) || messages === undefined ? (
        <p className="error-message"> Erreur : donnes des messages non disponibles.</p>
      ) : messages.length === 0 ? (
        <div className="no-message">
          <h3> Aucun message pour le moment</h3>
          <p>Connectez-vous pour publier le premier message.</p>
        </div>
      ) : (
        messages.map((message) => (
          <div key={message.id} className="message-card">
            <h3>{message.tittle}</h3>
            <p>{message.content}</p>
            <p className="author">
              Expdi par : {message.User?.email || 'Utilisateur inconnu'}
            </p>

            {message.media && message.media.length > 0 && (
              <div>
                <h4> Mdias associs :</h4>
                <div className="media-grid">
                  {message.media.map((file) => (
                    <div key={file.filename} className="media-item">
                      {file.type === 'image' ? (
                        <img
                          src={`https://ppacilyoncentre.com/uploads/images/${file.filename}`}
                          alt="Message media"
                        />
                      ) : (
                        <video controls>
                          <source
                            src={`https://ppacilyoncentre.com/uploads/videos/${file.filename}`}
                            type="video/mp4"
                          />
                          Votre navigateur ne supporte pas la lecture des vidos.
                        </video>
                      )}
                    </div>
                  ))}
                </div>
              </div>
            )}
          </div>
        ))
      )}
    </div>
  );
};

export default MessageList;
